	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"/2018-7-15-portrait-08e6f7b0fdbd25f13dd677241a60c3d2.jpg\" alt=\"Portrait of a model named KT\">\nPortrait of a model named KT</p>\n<p><img src=\"/2018-7-15-figure-91b50d7acf14b770b0ccb9e451e30794.jpg\" alt=\"God contemplating the fate of Donald Trump\">\nGod contemplating the fate of Donald Trump</p>\n<p>I’m getting better at being less representational in my art. It’s like swimming in the ocean and gradually getting comfortable with being farther and farther from shore. Eventually I’ll let go completely.</p>\n<p>The first is from the weekly portrait class. I’m happy about the more visible brush strokes that still manage to suggest form.</p>\n<p>The second is from a figure drawing class. It reminded me of the Calvin and Hobbes strips where Calvin imagines himself as the vengeful god of his imaginary universe, so I tried to emulate Waterson’s style in those strips.</p>","excerpt":"\nPortrait of a model named KT \nGod contemplating the fate of Donald Trump I’m getting better at being less representational in my art. It’s like swimming in the ocean and gradually getting comfortable with being farther and farther from shore…","frontmatter":{"date":"July 15, 2018","path":"/blog/2018/7/15","title":"Let's get abstract","category":"life, the universe and everything"}}},"pathContext":{}}

